<template>
  <div class="min-vh-100 bg-light py-5">
    <b-container class="bg-white rounded-4 shadow-lg p-4 mx-auto" style="max-width: 700px;">
      <h1 class="text-primary text-center mb-4">üìñ Public Journal View</h1>

      <div v-if="entries.length">
        <b-card
          v-for="(entry, index) in entries"
          :key="entry.entryId || index"
          class="mb-3"
          border-variant="primary"
          bg-variant="light"
          text-variant="dark"
        >
          <small class="text-muted mb-2 d-block" style="font-family: monospace;">
            {{ formatDate(entry.createdAt) }}
          </small>
          <p style="white-space: pre-line;">{{ entry.text }}</p>
        </b-card>
      </div>

      <div v-else class="text-center text-muted fw-semibold">
        No public journal entries available.
      </div>
    </b-container>
  </div>
</template>

<script>
export default {
  name: "JournalPublicView",
  data() {
    return {
      entries: [],
    };
  },
  mounted() {
    fetch("https://7oextb26ybeuoptz5f355jqphq0zjhva.lambda-url.us-east-1.on.aws")
      .then((res) => res.json())
      .then((data) => {
        if (Array.isArray(data)) {
          // Sort by createdAt descending
          this.entries = data.sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt));
        } else {
          this.entries = [];
        }
      })
      .catch((err) => {
        console.error("Public view fetch error:", err);
        this.entries = [];
      });
  },
  methods: {
    formatDate(date) {
      return new Date(date).toLocaleString("en-US", {
        dateStyle: "medium",
        timeStyle: "short",
      });
    },
  },
};
</script>
